body {
  margin: 0;
}
li{list-style-type:none }

ul{padding-left: 0}
.App {
  width: 100vw;
  overflow: hidden;
}
.card-a{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  border:0;


}
.card-b{
  overflow: auto;
}
.first-plan, .second-plan, .third-plan {
 padding: 0;
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
}
.second-plan{

  justify-content: space-around;

}
.nav-link{
  padding: 0;
}
.surface{
display: flex;
  padding: 3%;
  height: 300px;
  background-color: rgba(134,111,220,0.55);
  border-radius: 10%;
  width: 80vw;
  overflow: scroll;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  align-content: space-between;
}
.icons{
  width: 44px;
  height: 44px;
}
.surface::-webkit-scrollbar {
  width: 0;
}
.box{
  margin: 16px;
width: 200px;
  height: 200px;
  border-radius: 10%;
  overflow: hidden;
  display: flex;
  justify-content: center;
}
/*.first-plan{
  background-image: url("./img/sky.jpg");
  color: white;
}

.third-plan{
  background-image: url("./img/winter.jpg");
  color: black;
}*/
.text {
  text-align: center;
position: fixed;
}

.back-gr {
  display: flex;
  position: fixed;
  flex-direction: column;
  width: 100vw;

}
.photo{
  width: 100vw;
}
.progress-icon {
  position: fixed;
  top:68px;
  right: 0;
  width: auto;
  height: 10vh;
}
@media (max-width: 480px) {
  .surface {
    display: grid;
    grid-column: auto;
  }
  .box{
    margin: 17px;}
}
.todo-header{
  display: flex;
  justify-content: space-between;
}
.swipe-box{
  display: flex;
  justify-content: center;
}